import React from "react";

import * as S from "./styles";
import * as C from "../../styles/components";

import { OrderCustomerInfoProps } from "../../interfaces/components";

const OrderCustomerInfo: React.FC<OrderCustomerInfoProps> = ({customerForm, setCustomerForm}) => {
  return (
    <C.OrderSection>
      <header></header>
      <S.CustomerInfo>
        <div className="content">
          <C.InputField gridColumn="span 2">
            
            <C.Input
              required
              type="text"
              width="100%"
              placeholder=""
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  lastName: e.target.value,
                })
              }
            />
          </C.InputField>
          <C.InputField gridColumn="span 2">
            
            <C.Input
              required
              type="text"
              width="100%"
              placeholder=""
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  firstName: e.target.value,
                })
              }
            />
          </C.InputField>
          <C.InputField gridColumn="span 2">
            
            <C.Input
              required
              type="text"
              width="100%"
              placeholder=""
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  middleName: e.target.value,
                })
              }
            />
          </C.InputField>
          <C.InputField gridColumn="span 3">
            
            <C.Input
              required
              type="text"
              width="100%"
              placeholder="+7900000000"
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  middleName: e.target.value,
                })
              }
            />
          </C.InputField>

          <C.InputField gridColumn="span 3">
            Email
            <C.Input
              required
              type="email"
              width="100%"
              placeholder="example@mail.ru"
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  email: e.target.value,
                })
              }
            />
          </C.InputField>
          <C.InputField gridColumn="span 6">
              
            <textarea
              placeholder="  "
              name=""
              id=""
              onChange={(e) =>
                setCustomerForm({
                  ...customerForm,
                  comment: e.target.value,
                })
              }
            ></textarea>
          </C.InputField>
        </div>
      </S.CustomerInfo>
    </C.OrderSection>
  );
};

export default OrderCustomerInfo;
