import React from "react";

import * as S from "./styles";
import * as C from "../../styles/components";
import { OrderDeliveryInfoProps } from "../../interfaces/components";
import { config } from "../../configs/config";

const OrderDeliveryInfo: React.FC<OrderDeliveryInfoProps> = ({
  activeType,
  setActiveType,
  deliveryInfo,
  setDeliveryInfo,
}) => {
  return (
    <C.OrderSection>
      <header></header>
      <S.DeliveryInfo>
        <div className="content">
          <S.selectDeliveryContainer>
            <S.SelectDeliveryBtn
              className={activeType === "delivery" ? "active" : undefined}
              onClick={() => setActiveType("delivery")}
            >
              <img
                src={`${window.location.origin}/assets/img/delivery/delivery.png`}
                alt=""
              />
              <div className="info-container">
                <p></p>
                <span>
                       {" "}
                  {config.deliveryCost} 
                </span>
              </div>
            </S.SelectDeliveryBtn>
            <S.SelectDeliveryBtn
              className={activeType === "pickup" ? "active" : undefined}
              onClick={() => setActiveType("pickup")}
            >
              <img
                src={`${window.location.origin}/assets/img/delivery/pickup.png`}
                alt=""
              />
              <div className="info-container">
                <p></p>
                <span>
                           {" "}
                  {config.information.address}
                </span>
              </div>
            </S.SelectDeliveryBtn>
          </S.selectDeliveryContainer>
          {activeType === "delivery" && (
            <S.DeliveryForm>
              <C.InputField gridColumn="span 1">
                
                <C.Input
                  required
                  type="text"
                  width="100%"
                  placeholder="--"
                  onChange={(e) =>
                    setDeliveryInfo({
                      ...deliveryInfo,
                      city: e.target.value,
                    })
                  }
                />
              </C.InputField>
              <C.InputField gridColumn="span 1">
                
                <C.Input
                  required
                  type="text"
                  width="100%"
                  placeholder=". "
                  onChange={(e) =>
                    setDeliveryInfo({
                      ...deliveryInfo,
                      street: e.target.value,
                    })
                  }
                />
              </C.InputField>
              <C.InputField gridColumn="span 1">
                
                <C.Input
                  required
                  type="text"
                  width="100%"
                  placeholder="115"
                  onChange={(e) =>
                    setDeliveryInfo({
                      ...deliveryInfo,
                      streetNumber: e.target.value,
                    })
                  }
                />
              </C.InputField>
              <C.InputField gridColumn="span 1">
                
                <C.Input
                  required
                  type="text"
                  width="100%"
                  placeholder="10"
                  onChange={(e) =>
                    setDeliveryInfo({
                      ...deliveryInfo,
                      flatNumber: e.target.value,
                    })
                  }
                />
              </C.InputField>
            </S.DeliveryForm>
          )}
        </div>
      </S.DeliveryInfo>
    </C.OrderSection>
  );
};

export default OrderDeliveryInfo;
